import React from 'react';
import { Button, TabBar,Icon } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import '../iconfont/iconfont.css'
class AppTab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '/home',
            hidden: false,
            fullScreen: false,
        };
    }
    render() {
        const pathname = this.props.location.pathname;
        return (
            <div style={{
                position: 'fixed',
                bottom: 0,
                width: '100%',
            }} >

                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white"
                    hidden={false}
                >
                    <TabBar.Item
                        title="首页"
                        key="Life"
                        icon={<i className="iconfont">&#xe751;</i>
                        }
                        selectedIcon={<i className="iconfont">&#xe750;</i>

                        }
                        selected={pathname === '/home'}
                        onPress={() => {
                            this.props.history.push('/home')
                        }}
                        data-seed="logId"
                    >


                    </TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i className="iconfont">&#xe78b;</i>
                        }
                        selectedIcon={
                            <i className="iconfont">&#xe78a;</i>
                        }
                        title="收藏"
                        key="Koubei"
                        selected={pathname === '/shopcar'}
                        onPress={() => {
                            this.props.history.push('/shopcar')
                        }}
                        data-seed="logId1"
                    >

                    </TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i className="iconfont">&#xe737;</i>
                        }
                        selectedIcon={
                            <i className="iconfont">&#xe7ea;</i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={pathname === '/dingdan'}
                        onPress={() => {

                            this.props.history.push('/dingdan')

                        }}
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className="iconfont">&#xe75e;</i>}
                        selectedIcon={<i className="iconfont">&#xe75d;</i>}
                        title="我的淘宝"
                        key="my"
                        selected={pathname === '/my'}
                        onPress={() => {
                            this.props.history.push('/my')

                        }}
                    >

                    </TabBar.Item>

              </TabBar>
            </div>
        );
    }
}
export default withRouter(AppTab);