import React from 'react'
import getlogin from './getlogin'
import {connect} from 'react-redux'

const login = (props) => {
    const login1=()=>{
        props.dispatch(getlogin())

        setTimeout(props.history.goBack(), 3000)
      }
    return (
        <div style={{marginLeft:'90px',marginTop:'200px'}}>
            <input type='text' placeholder='用户名' style={{width:'180px',height:'30px'}}/>
            <input type='password'placeholder='密码' style={{width:'180px',height:'30px',marginTop:'10px',marginBottom:'10px'}}/>
            <button style={{width:'190px',height:'40px',backgroundColor:'#87CEFA'}} onClick={login1}>登录</button>
        </div>
    )
}
const mapStateToProps=(state)=>({
    username:state.loginReducer.username
})
export default connect(mapStateToProps)(login)