import React, { Component } from 'react'
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Home from './container/AppHome';
import DingDan from './container/AppDingDan';
import Car from './container/shoucang';
import My from './container/AppMy';
import Search from './container/Search';
import XiangQing from './container/XiangQing';
import AppTab from './container/AppTab';
import Login from './container/Login';
import {Detail} from './container/Things';
import Store from './container/Store';
import 'antd-mobile/dist/antd-mobile.css';
import {Provider} from 'react-redux'
const App = (props) => {
  console.log(props.location);
  return <Provider store={Store}>
  <Router>
      <Switch>
          {/* 首页 */}
          <Route exact path='/home' component={Home}></Route>
          <Route path='/search' component={Search}></Route>
          {/* 购物车 */}
          <Route path='/shopcar' component={Car}></Route>
          {/* 订单列表 */}
          <Route path='/dingdan' component={DingDan}></Route>
          {/* 我的淘宝 */}
          <Route path="/my" component={My} />
          <Route path='/home/docu/:type' component={Detail}/>
          <Route path='/home/login' component={Login} ></Route>
          <Redirect from='/*' to='/home' />
      </Switch>
  </Router>
  </Provider>
}
export default App;
