import React from 'react';
import {withRouter} from 'react-router-dom'
import {connect} from 'react-redux'
import '../iconfont/iconfont.css'
import 'antd-mobile/dist/antd-mobile.css';
const Dtal=(props)=>{
  const img=()=>{
    for(let i=0;i<props.shoucang.length;i++){
      if(props.shoucang[i]===props.val){
        return 'iconfont icon-biaoxingfill'
      }
    }
    return 'iconfont icon-biaoxing'
  }
  const funlog=(e)=>{
    if(props.username){
      //console.log(img);
      if(e.target.className == 'iconfont icon-biaoxingfill'){
        e.target.className = 'iconfont icon-biaoxing'
        props.dispatch({type:'DEL_TODO',value:props.val})
      }else{
        props.dispatch({type:'ADD_TODO',value:props.val})
        e.target.className = 'iconfont icon-biaoxingfill';
      }
    }else{
      props.history.push('/home/login')
    }
  }
  console.log(props);
  return (
    <div style={{
        position: 'fixed',
        bottom: 0,
        width: '100%',
        height: '60px',
        display:'flex',
        flex:'row',
        backgroundColor:'white'
    }}>
         <a className='iconfont icon-dianpu' style={{display:'inline-block',marginRight:'25px',marginTop:'10px'}}><div style={{fontSize:15}}>店铺</div></a>
         <a className='iconfont icon-wangwang' style={{display:'inline-block',marginRight:'25px',marginTop:'10px'}}><div style={{fontSize:15}}>客服</div></a>
         <a className={img()} style={{display:'inline-block',marginRight:'35px',marginTop:'10px'}} onClick={(e)=>funlog(e)}>收藏</a>
         <div style={{height:'40px',flex:'3',marginTop:'10px',backgroundColor:'#ffc500',borderBottomLeftRadius:'20px',borderTopLeftRadius:'20px'}}>
            <p style={{textAlign:'center',lineHeight:'15px',color:'white'}}>加入购物车</p>
        </div>
        <div style={{height:'40px',flex:'3',marginTop:'10px',backgroundColor:'#fe5b09',borderBottomRightRadius:'20px',borderTopRightRadius:'20px'}}>
            <p style={{textAlign:'center',lineHeight:'15px',color:'white'}}>立即购买</p>
         </div>
    </div>
  );
}
const mapStateToProps=(state)=>({
  username:state.loginReducer.username,
  shoucang:state.shoucangReducer
})
export default connect(mapStateToProps)(withRouter(Dtal))