import { SearchBar, WingBlank } from 'antd-mobile'
import React from 'react'

//商品搜索页面

const Search = (props) => {
    return (
        <div>
            <SearchBar
                placeholder="宝贝"
                showCancelButton
                onCancel={() => {
                    props.history.go(-1);
                }} />


        </div>
    )
}

export default Search