import React, { useEffect } from 'react'
import { NavLink, Route, Redirect, useHistory, useLocation, useParams, useRouteMatch, withRouter } from 'react-router-dom'
import { Button, Icon, NavBar } from 'antd-mobile';
import '../iconfont/iconfont.css'

const XiangQing = (props) => {
    return <div>
        <li
            style={{
                listStyle: 'none',
                display: 'inline-block',
                width: '100%',
                height: '300px',

            }}
        >
            <div style={{ position: 'absolute' }}><button
                style={{ backgroundColor: 'grey', opacity: '1', width: '40px', height: '40px', borderRadius: '50%', opacity: '0.5', marginLeft: '20px', marginTop: '20px' }}
                mode="light"
                onClick={() => props.history.go(-1)}
            >＜</button></div>
            <div style={{ position: 'relative', zIndex: '-1' }}>
                <img style={{ width: '100%', height: '300px' }} src={props.location.src} />
            </div>
        </li>
        <div style={{
            position: 'fixed',
            bottom: 0,
            width: '100%',
            height: '60px',
            display:'flex',
            flex:'row',
            backgroundColor:'white'
        }}>
            <div style={{height:'60px',flex:'1',}}>
                <img style={{height:'25px',width:'60%',marginLeft:'20%',marginTop:'5px'}} src={require('./img/shop.png')}/>
                <p style={{textAlign:'center',lineHeight:'2px'}}>店铺</p>
            </div>
            <div style={{height:'60px',flex:'1'}}>
            <img style={{height:'25px',width:'60%',marginLeft:'20%',marginTop:'5px'}} src={require('./img/wang.png')}/>
                <p style={{textAlign:'center',lineHeight:'2px'}}>客服</p>
            </div>
            <div style={{height:'60px',flex:'1'}}>
            <img style={{height:'25px',width:'60%',marginLeft:'20%',marginTop:'5px'}} src={require('./img/favor.png')}/>
                <p style={{textAlign:'center',lineHeight:'2px'}}>收藏</p>
            </div>
            <div style={{height:'40px',flex:'3',marginTop:'10px',backgroundColor:'#ffc500',borderBottomLeftRadius:'20px',borderTopLeftRadius:'20px'}}>
                <p style={{textAlign:'center',lineHeight:'15px',color:'white'}}>加入购物车</p>
            </div>
            <div style={{height:'40px',flex:'3',marginTop:'10px',backgroundColor:'#fe5b09',borderBottomRightRadius:'20px',borderTopRightRadius:'20px'}}>
            <p style={{textAlign:'center',lineHeight:'15px',color:'white'}}>立即购买</p>
            </div>
        </div>

    </div>
}

export default withRouter(XiangQing)