import { Carousel, WingBlank } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import React from 'react'
class Lunbo extends React.Component {
  state = {
    data: ['./img/lb1.jpg','./img/lb2.jpg','./img/lb3.jpg','./img/lb4.jpg','./img/lb5.jpg'],
    imgHeight: 220,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: ['https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=2853553659,1775735885&fm=26&gp=0.jpg',
				'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1603634507052&di=f5d86a9226fd56fbf5808f08b91abba1&imgtype=0&src=http%3A%2F%2Ff.hiphotos.baidu.com%2Fzhidao%2Fpic%2Fitem%2Fd1a20cf431adcbefe30c97d9aeaf2edda3cc9f31.jpg',
				'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1603634507052&di=6b60f644e429ce0c9227f27503dd4a5d&imgtype=0&src=http%3A%2F%2Fc.hiphotos.baidu.com%2Fzhidao%2Fpic%2Fitem%2Fa044ad345982b2b7dabd6f0633adcbef77099bd5.jpg',
				'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1603634507051&di=772f9fb38628ed4258360458afa39632&imgtype=0&src=http%3A%2F%2Fa1.att.hudong.com%2F08%2F22%2F01300000242726125670225939875.jpg',
				'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1603634507049&di=508b21d030ef28f09d238c6228f20c34&imgtype=0&src=http%3A%2F%2Fc.hiphotos.baidu.com%2Fzhidao%2Fpic%2Fitem%2Ff3d3572c11dfa9ecc052be8060d0f703918fc12d.jpg'],
      });
    }, 1000);
  }
  componentWillUnmount() {
    this.setState = () => false;
}
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay={true}
          infinite={true}
          style={{marginTop:'40px'}}
        >
          {this.state.data.map(val=> (
            <a
              key={val.toString()}
              style={{ display: 'inline-block', width: '100%', height: 220+'px' }}
            >
              <img
                src={`${val}`}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default withRouter(Lunbo);